* {
  background: #000;
  color: #ff003c;
}
.main {
  color: #ff003c;
  text-align: center;
  width: 32rem;
  margin: 3rem auto 6rem;
}
.headingLg {
  font-size: 1.8rem;
  line-height: 1.6;
  margin: 1rem 0;
}

.headingMd {
  font-size: 1rem;
  line-height: 1.5;
  margin: 1rem 0;
  color: #00f0ff;
}
.icon {
  border-radius: 9999px;
  width: 150px;
  height: 150px;
  background: #222;
}
.btn {
  align-items: center;
  justify-content: center;
  padding: 8px 16px;
  margin: 3px auto 9px;

  background: #000;
  color: #fcee09;

  cursor: pointer;
  font-size: .85rem;

  border: 1px solid #fcee09;
  text-decoration: none;
}


@media screen and (max-width: 480px) {
  .main {
    color: #ff003c;
    text-align: center;
    align-items: center;
    width: 23rem;
    margin: 2rem auto 4rem;
  }
}
